export default {
	data() {
		return {
			StatusBar: this.StatusBar,
			CustomBar: this.CustomBar,
			TabCur: '1',
		};
	},
	name: 'custom-nav',
	computed: {
		style() {
			var StatusBar = this.StatusBar;
			var CustomBar = this.CustomBar;
			var style = `height:${CustomBar}px;padding-top:${StatusBar}px;`;
			return style
		}
	},
	props: {
		isBack: {
			type: [Boolean, String],
			default: true
		},
		textRight: {
			type: String,
			default: ''
		},
		textTitle: {
			type: String,
			default: '',
		},
		tabsList: {
			type: Array,
			default: null
		}
	},
	methods: {
		BackPage() {
			uni.navigateBack({
				delta: 1
			});
		},
		rightHandle() {
			console.log(this.textRight)
			if (!this.textRight) {
				return false;
			}
			this.$emit('right');
		},
		tabSelect(item) {
			this.TabCur = item.id;
		}
	}
}
